.App {
  visibility: hidden;
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.backgroundContainer {
  position: relative; /* Это необходимо, чтобы создать контекст позиционирования для абсолютно позиционированных элементов внутри него */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.backgroundImage {
  max-width: 100%;
  max-height: 100%;
  z-index: 1;
}

.fogGif {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 34.3%;
  z-index: 2;
  opacity: 0.2;
}

.cube {
  position: absolute;
  height: 100%;
  z-index: 3;
  animation: shake 2.5s infinite;
}

.dateText {
  position: absolute;
  top: 20.5%;
  left: 50%;
  font-family: 'MyCustomFont', sans-serif;  /* 'MyCustomFont' - это имя, которое вы дали шрифту в @font-face */
  font-weight: normal;
  transform: translate(-50%, -50%);
  color: white;
  opacity: 0.4;
  word-wrap: break-word;
  text-align: center;
  user-select: none; /* стандартный синтаксис */
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  z-index: 3;
}

.dateText2 {
  position: absolute;
  height: 100%;
  font-family: 'MyCustomFont', sans-serif;  /* 'MyCustomFont' - это имя, которое вы дали шрифту в @font-face */
  font-weight: normal;
  transform: translate(6%, 91.39%);
  color: white;
  opacity: 0.8;
  word-wrap: break-word;
  text-align: center;
  user-select: none; /* стандартный синтаксис */
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  z-index: 3;
}

@keyframes shake {
  0%, 100% {
    transform: translate(-21%, 45%) scale(0.06) scaleX(1.3) rotate(0deg);
    opacity: 0.3;
  }
  8.33% {
    transform: translate(-21%, 45%) scale(0.08) scaleX(1.3) rotate(10deg);
    opacity: 1;
  }
  16.67% {
    transform: translate(-21%, 45%) scale(0.06) scaleX(1.3) rotate(-10deg);
    opacity: 1;
  }
  25% {
    transform: translate(-21%, 45%) scale(0.08) scaleX(1.3) rotate(10deg);
    opacity: 1;
  }
  33.33% {
    transform: translate(-21%, 45%) scale(0.06) scaleX(1.3) rotate(-10deg);
    opacity: 1;
  }
  41.67% {
    transform: translate(-21%, 45%) scale(0.08) scaleX(1.3) rotate(10deg);
    opacity: 1;
  }
  90% {
    opacity: 0.3;
  }
}

/* Уменьшаем размер шрифта на меньших экранах */
@media (max-width: 1400px) {
  .dateText {
    font-size: 4.4rem;
  }
}

/* Уменьшаем размер шрифта на меньших экранах */
@media (max-width: 1250px) {
  .dateText {
    font-size: 4.0rem;
  }
}

@font-face {
  font-family: 'MyCustomFont';  /* Имя, которое вы будете использовать для ссылки на шрифт в CSS */
  src: url('./assets/fonts/1.ttf') format('opentype');  /* Путь к вашему файлу шрифта */
  font-style: normal;
}
